export default {
	data() {
		const currentDate = this.getDate({
			format: true
		});
		return {
			bian: true, //更多选项boo
			shui: true, //报价含税
			yunfei: true, //报价含税
			fapiao: ['增值税发票', '其他发票'], //发票数组
			fapiaoindex: 0, //发票index
			date: currentDate, //当前日期
			date02: currentDate, //当前日期
			shengming: true, //声明
			jiadata: [
				{
					cao: '',
					images: ''
				}
			],
			adressdata:[],//地址data
			state:true, //是否可以提交
		};
	},
	methods: {
		// 展示发票要求
		showyaoqiu: function() {
			var that = this;
			uni.showActionSheet({
				itemList: that.fapiao,
				success: function(res) {
					that.fapiaoindex = res.tapIndex;
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		// 修改报价要求
		change: function(type) {
			if (type == 0) {
				this.shui = !this.shui;
			} else if (type == 1) {
				this.yunfei = !this.yunfei;
			} else if (type == 2) {
				this.shengming = !this.shengming;
			}
		},
		//点击删除，删除添加内容
		shan: function(index) {
			this.jiadata.splice(index, 1);
		},
		//添加内容，点击展示下面内容
		jia: function() {
			this.jiadata.push({
				cao: '',
				images: ''
			});
		},
		//跳转页面
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		//点击展开更多默认选项 展示下面内容
		moren: function() {
			this.bian = !this.bian;
		},
		// 上传图片
		choseimage: function(index) {
			var that = this;
			//调取相册
			uni.chooseImage({
				count: 1, //默认9
				success: function(res) {
					console.log(res.tempFilePaths[0]);
					uni.uploadFile({
						url: getApp().globalData.byurl + '/buyer/base/upload', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							scene: 'idcard'
						},
						success: uploadFileRes => {
							that.jiadata[index].images  = JSON.parse(uploadFileRes.data).url;
						},
						fail: err => {
							console.log(err);
						}
					});
				},
				fail: function(fa) {
					//提示框
					uni.showToast({
						title: '失败，请重试',
						duration: 2000
					});
				}
			});
		},
		// 获取当前时间
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year;
			} else if (type === 'end') {
				year = year + 10;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		// 获取截止报价日期回调
		bindDateChange: function(e) {
			this.date = e.target.value;
		},
		// 获取最迟收货日期回调
		bindDateChange02: function(e) {
			this.date02 = e.target.value;
		},
		// 查询我的收货地址
		myajax:function(){
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    header: {
			        'custom-header': 'hello' //自定义请求头信息
			    },
			    success: (res) => {
			        console.log(res.data);
			        this.adressdata =res.data
			    }
			});
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onShow: function(){
		console.log(uni.getStorageSync('userid'))
		if(uni.getStorageSync('userid')==''){
			this.state=false
			uni.showToast({
				title:'请先进行登陆',
				icon:'none'
			})
		}else{
			this.myajax()
		}
	}
};
